import { createApp } from 'vue'
import App from './App.vue'
import myHeader from "./components/myHeader.vue"
import Block from "./components/Block.vue"
import Chitate from "./components/Chitate.vue"
import Content from "./components/Content.vue"
import Clients from "./components/Clients.vue"
import myFooter from "./components/myFooter.vue"

let Site = createApp(App);

Site.component("myHeader", myHeader);
Site.component("myBlock", Block);
Site.component("myChitate", Chitate);
Site.component("content", Content);
Site.component("clients", Clients);
Site.component("myFooter", myFooter);

Site.mount('#app');
